:root {
    --major-font-family: 'Montserrat', sans-serif;
    
    --primary-color: #111518;
    --secondary-text-color: #B9B9B9;
    --dark-theme-text-color: #fff;
    --alternative-text-color: #3d3d3d;
    --accent-color: #FF6B09;

    --duration: 300ms;
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}